import { toast } from "react-toastify";
import "./style.css";
import { useNavigate } from "react-router";
import { useEffect, useState } from "react";

const ProductsList = ({ products, user }) => {
  const navigate = useNavigate();
  const [communityProducts, setCommunityProducts] = useState([]);
  const [proProducts, setProProducts] = useState([]);

  useEffect(() => {
    const communityProducts = products.filter(
      (product) => product.type === "community"
    );
    const proProducts = products.filter(
      (product) => product.type === "professional"
    );
    setCommunityProducts(communityProducts);
    setProProducts(proProducts);
  }, [products]);

  const handleDownload = (product) => {
    if (!user) {
      toast.warning("Please login to download the trial version");
      return;
    }

    if (product.type === "professional") {
      navigate("/contact");
      return;
    }

    const link = document.createElement("a");
    link.href = `http://localhost:9000/${product.file}`;
    link.download = `product.name.zip`;
    link.click();
  };

  return (
    <>
      <section className="container-fluid price">
        <div className="container">
          <h5 className="text-center pricing-h">Products</h5>
          <h3 className="text-center fw-bold main-h">Community</h3>
          <div className="row justify-content-around gap-5">
            {communityProducts.map((product) => {
              return (
                <div className="col-md-3 pricing-col" key={product._id}>
                  <div
                    className="pricing-title"
                    // style={{ backgroundColor: "#220b4f" }}
                  >
                    <h3>{product.name}</h3>
                  </div>
                  <div className="pricing-body">
                    <div className="feature">
                      <p className="pt-3 px-1">{product.description}</p>
                    </div>
                  </div>
                  <p
                    style={{ textTransform: "capitalize" }}
                    className="product-type"
                  >
                    {product.type}
                  </p>
                  <div className="pricing-btn">
                    <button
                      style={{ backgroundColor: "#220b4f" }}
                      onClick={() => handleDownload(product)}
                    >
                      Download Trial
                    </button>
                  </div>
                </div>
              );
            })}
            {/* <div className="col-md-3 pricing-col">
              <div className="pricing-title">
                <h3>Professional</h3>
              </div>
              <div className="pricing-body">
                <div className="feature">
                  <ul className="list-unstyled mt-3">
                    <li>Full Access</li>
                    <li>Proper Report</li>
                    <li>24*7 Hours Support</li>
                    <li>100% Security Assurance</li>
                    <li>unlimited features</li>
                  </ul>
                </div>
              </div>
              <div className="price-item">
                <span className="currency">â‚¹</span>
                <span className="tk">5000</span>
                <span className="month">One Time</span>
              </div>
              <div className="pricing-btn">
                <a href=".">select plan</a>
              </div>
            </div> */}
          </div>

          <hr style={{ opacity: 0.1 }} />
          <h3 className="text-center fw-bold main-h mt-5">Professional</h3>
          <div className="row justify-content-around gap-5">
            {proProducts.map((product) => {
              return (
                <div className="col-md-3 pricing-col" key={product._id}>
                  <div
                    className="pricing-title"
                    // style={{ backgroundColor: "#220b4f" }}
                  >
                    <h3>{product.name}</h3>
                  </div>
                  <div className="pricing-body">
                    <div className="feature">
                      <p className="pt-3 px-1">{product.description}</p>
                    </div>
                  </div>
                  <p
                    style={{ textTransform: "capitalize" }}
                    className="product-type"
                  >
                    {product.type}
                  </p>
                  <div className="pricing-btn">
                    <button
                      style={{ backgroundColor: "#220b4f" }}
                      onClick={() => handleDownload(product)}
                    >
                      Download Trial
                    </button>
                  </div>
                </div>
              );
            })}
          </div>
        </div>
      </section>
    </>
  );
};

export default ProductsList;
